export default "/*--- */\n/* text color*/\n/* for fill background-color */\n.info-box {\n  width: 100%;\n  border-radius: 6px;\n  margin-bottom: 20px;\n  padding: 15px 15px 15px 25px;\n}\n.info-box-icon {\n  float: left;\n  height: 70px;\n  width: 70px;\n  text-align: center;\n  font-size: 30px;\n  line-height: 74px;\n  background: rgba(0, 0, 0, 0.2);\n  border-radius: 100%;\n}\n.info-box-content {\n  padding: 10px 10px 10px 0;\n  margin-left: 90px;\n}\n.info-box-number {\n  font-weight: 500;\n  font-size: 21px;\n}\n.info-box-body,\n.progress-description {\n  font-size: 2.2rem;\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  overflow: hidden;\n  flex-wrap: wrap;\n  height: 100%;\n}\n.info-icon {\n  width: 100%;\n  height: auto;\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n  align-items: flex-end;\n  margin-bottom: 8px;\n}\n.info-icon img {\n  width: 45px;\n  height: 45px;\n  padding-right: 12px;\n}\n.info-box-body h2 {\n  font-size: 1.6rem;\n  font-weight: 700;\n  letter-spacing: 0.5px;\n}\n.info-box-body p {\n  margin-bottom: 0px;\n  text-align: right;\n  font-size: 32px;\n}\n.info-text {\n  width: 100%;\n  text-align: right;\n}\n.progress {\n  border: 0;\n  background-image: none;\n  filter: none;\n  box-shadow: none;\n  height: 8px;\n  border-radius: 0 !important;\n  margin: 0;\n}\n.info-box .progress {\n  background: rgba(0, 0, 0, 0.2);\n  margin: 5px -10px 5px 0;\n  height: 2px;\n}\n.info-box .progress,\n.info-box .progress .progress-bar {\n  border-radius: 0;\n}\n@media (max-width: 991.98px) {\n  .info-box {\n    height: auto;\n  }\n}\n@media (max-width: 768.98px) {\n  .info-icon img {\n    width: 45px;\n    height: 45px;\n  }\n}\n/* dark theme style start */\n.dark-theme .info-box {\n  background: #212121;\n  border: 1px solid #555;\n}\n.dark-theme .info-box-body h2 {\n  color: #fff;\n}\n/* dark theme style close */\n/*# sourceMappingURL=data:application/json;base64, */";