export default "/*--- */\n/* text color*/\n/* for fill background-color */\n.loginwrapper {\n  position: fixed;\n  height: 100vh;\n  width: 100%;\n  top: 0;\n  left: 0;\n  background-color: #e8ebe9;\n  z-index: 99;\n  overflow-y: auto;\n}\n.limiter {\n  width: 100%;\n  margin: 0 auto;\n}\n.container-login {\n  width: 100%;\n  min-height: 70vh;\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  align-items: center;\n  padding: 25px 15px 15px;\n  background: transparent;\n}\n.wrap-login {\n  width: 390px;\n  background: #fff;\n  border-radius: 4px;\n  overflow: hidden;\n  padding: 35px 30px 32px 30px;\n  box-shadow: 0 0 30px 1px rgba(0, 0, 0, 0.1);\n}\n.login-form {\n  width: 100%;\n}\n/* login header start */\n.login-header {\n  display: flex;\n  flex-direction: row;\n  /* flex-wrap: wrap; */\n  justify-content: center;\n  align-items: center;\n  padding-bottom: 36px;\n}\n.login-logo {\n  width: 80px;\n  height: auto;\n  overflow: hidden;\n  margin: 0 auto 20px;\n}\n.login-form-title {\n  display: block;\n  font-size: 2.6rem;\n  font-weight: 700;\n  color: #333333;\n  text-align: center;\n  margin-bottom: 20px;\n  /* login header close */\n}\n.wrap-input-field {\n  width: 100%;\n  position: relative;\n  margin-bottom: 24px;\n}\n.input-field {\n  font-size: 15px;\n  color: #555555;\n  line-height: 1.2;\n  display: block;\n  width: 100%;\n  height: 45px;\n  background: transparent;\n  padding: 8px 12px;\n  border: 1px solid #adadad;\n  border-radius: 4px;\n  transition: all 0.4s ease;\n}\n.input-field:focus {\n  border-color: #2c7fc6;\n}\n.container-login-form-btn {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  padding-top: 13px;\n  margin-bottom: 20px;\n  width: 100%;\n}\n.wrap-login-form-btn {\n  width: 100%;\n  margin-bottom: 14px;\n  display: block;\n  position: relative;\n  margin-left: auto;\n  margin-right: auto;\n  z-index: 1;\n}\n.login-form-btn {\n  position: relative;\n  font-size: 15px;\n  color: #fff;\n  background: #2c7fc6;\n  border: 2px solid #2c7fc6;\n  line-height: 1.2;\n  text-transform: uppercase;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0 20px;\n  width: 100%;\n  height: 50px;\n  border-radius: 4px;\n  transition: all 0.4s ease;\n}\n.wrap-login-form-btn .login-form-btn:hover,\n.login-form-btn:focus {\n  color: #222;\n  background: transparent;\n  border: 2px solid #2c7fc6;\n}\n.footer-link,\n.footer-link a {\n  text-align: center;\n  margin: 10px auto;\n  text-transform: capitalize;\n}\n.footer-link a:hover {\n  color: #2c7fc6;\n  text-decoration: underline;\n}\n@media (max-width: 768.98px) {\n  .container-login {\n    padding: 40px 15px;\n  }\n\n  .wrap-login {\n    padding: 35px 25px 32px 25px;\n  }\n\n  .wrap-input-field {\n    margin-bottom: 22px;\n  }\n\n  .login-header {\n    padding-bottom: 22px;\n    display: flex;\n  }\n\n  .login-form-title {\n    font-size: 2rem;\n  }\n\n  .login-logo {\n    width: 50px;\n  }\n}\n/* dark theme style start */\n.dark-theme .wrap-login {\n  background: #212121 !important;\n  border: 1px solid #555;\n}\n.dark-theme .loginwrapper {\n  background: #212121 !important;\n}\n.dark-theme .wrap-input-field label.control-label {\n  color: #fff;\n}\n.dark-theme .wrap-login-form-btn .login-form-btn:hover {\n  color: #fff;\n}\n.dark-theme span.login-form-title {\n  color: #fff;\n}\n.dark-theme .input-field {\n  color: #fff !important;\n}\n/* dark theme style close */\n/*# sourceMappingURL=data:application/json;base64, */";