export default "/*--- */\n/* text color*/\n/* for fill background-color */\n.loginwrapper {\n  position: fixed;\n  height: 100vh;\n  width: 100%;\n  top: 0;\n  left: 0;\n  background-color: #e8ebe9;\n  z-index: 99;\n  overflow-y: auto;\n}\n.limiter {\n  width: 100%;\n  height: 100%;\n  margin: 0 auto;\n}\n.container-login {\n  width: 100%;\n  height: 100%;\n  min-height: 70vh;\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  align-items: center;\n  padding: 25px 15px 15px;\n  background: transparent;\n}\n.login-form {\n  width: 100%;\n}\n/* login header start */\n.login-header {\n  display: flex;\n  flex-direction: row;\n  /* flex-wrap: wrap; */\n  justify-content: center;\n  align-items: center;\n  padding-bottom: 20px;\n}\n.login-logo {\n  width: 80px;\n  height: auto;\n  overflow: hidden;\n  margin: 0 12px 0 0;\n}\n.login-form-title {\n  display: block;\n  font-size: 2.6rem;\n  font-weight: 700;\n  color: #333333;\n  text-align: center;\n  margin-bottom: 14px;\n}\n/* login header close */\n.wrap-input-field {\n  width: 100%;\n  position: relative;\n  border-bottom: 2px solid #adadad;\n  margin-bottom: 22px;\n}\n.input-field {\n  font-size: 15px;\n  color: #555555;\n  line-height: 1.2;\n  display: block;\n  width: 100%;\n  height: 45px;\n  background: transparent;\n  padding: 0 5px;\n  border: 0;\n}\n.focus-input-field {\n  position: absolute;\n  top: 0;\n  right: auto;\n  bottom: auto;\n  left: 0;\n  display: block;\n  width: 100%;\n  height: 100%;\n  pointer-events: none;\n}\n.focus-input-field::before {\n  content: \"\";\n  display: block;\n  position: absolute;\n  bottom: -2px;\n  left: 0;\n  width: 0;\n  height: 2px;\n  transition: all 0.4s ease;\n  background: #2c7fc6;\n}\n.input-field:focus + .focus-input-field::before {\n  width: 100%;\n}\n.container-login-form-btn {\n  display: flex;\n  flex-wrap: wrap;\n  justify-content: center;\n  padding-top: 13px;\n  margin-bottom: 20px;\n}\n.wrap-login-form-btn {\n  width: 100%;\n  display: block;\n  position: relative;\n  margin-left: auto;\n  margin-right: auto;\n  z-index: 1;\n}\n.login-form-btn {\n  position: relative;\n  font-size: 15px;\n  color: #fff;\n  background: #2c7fc6;\n  border: 2px solid #2c7fc6;\n  line-height: 1.2;\n  text-transform: uppercase;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 0 20px;\n  width: 100%;\n  height: 50px;\n  border-radius: 10px;\n  transition: all 0.4s ease;\n}\n.wrap-login-form-btn .login-form-btn:hover,\n.login-form-btn:focus {\n  color: #222;\n  background: transparent;\n  border: 2px solid #2c7fc6;\n}\n.footer-link,\n.footer-link a {\n  text-align: center;\n  text-transform: capitalize;\n}\n.footer-link a:hover {\n  color: #2c7fc6;\n  text-decoration: underline;\n  cursor: pointer;\n}\n.login-reg-panel {\n  position: relative;\n  text-align: center;\n  max-width: 1024px;\n  width: 60%;\n  right: 0;\n  left: 0;\n  margin: 0 auto;\n  height: 400px;\n  border-radius: 10px;\n  background-image: linear-gradient(to right top, #007bff, #3678fb, #4d74f6, #5d71f1, #6a6dec);\n}\n.white-panel {\n  position: absolute;\n  top: -50px;\n  width: 390px;\n  height: 500px;\n  right: calc(50% - 50px);\n  z-index: 0;\n  background: #fff;\n  border-radius: 10px;\n  overflow: hidden;\n  padding: 24px;\n  box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.1);\n  transition: 0.3s ease-in-out;\n}\n.white-panel .wrap-login {\n  background: transparent;\n  width: auto;\n  border-radius: 0;\n  border: 0;\n  padding: 0;\n  box-shadow: none;\n}\n.login-reg-panel input[type=radio] {\n  position: relative;\n  display: none;\n}\n.login-reg-panel {\n  color: #B8B8B8;\n}\n.login-reg-panel #label-login,\n.login-reg-panel #label-register {\n  border: 1px solid #9E9E9E;\n  padding: 5px 5px;\n  width: 150px;\n  display: block;\n  text-align: center;\n  border-radius: 10px;\n  cursor: pointer;\n  font-weight: 600;\n  font-size: 18px;\n}\n.login-info-box {\n  width: 40%;\n  padding: 0 50px;\n  top: 20%;\n  left: 0;\n  position: absolute;\n  text-align: left;\n}\n.register-info-box {\n  width: 40%;\n  padding: 0 50px;\n  top: 20%;\n  right: 0;\n  position: absolute;\n  text-align: left;\n}\n.right-log {\n  right: 50px !important;\n}\n.login-show,\n.register-show {\n  z-index: 1;\n  display: none;\n  opacity: 0;\n  transition: 0.3s ease-in-out;\n  color: #242424;\n  text-align: left;\n  padding: 0;\n}\n.show-log-panel {\n  display: block;\n  opacity: 1;\n}\n.login-show input[type=text],\n.login-show input[type=password] {\n  width: 100%;\n  display: block;\n  outline: none;\n}\n.login-show a {\n  display: inline-block;\n  padding: 10px 0;\n}\n.register-show input[type=text],\n.register-show input[type=password] {\n  width: 100%;\n  display: block;\n  outline: none;\n}\n.register-show input[type=button] {\n  max-width: 150px;\n  width: 100%;\n  background: #444444;\n  color: #f9f9f9;\n  border: none;\n  padding: 10px;\n  text-transform: uppercase;\n  border-radius: 2px;\n  float: right;\n  cursor: pointer;\n}\n.login-info-box,\n.register-info-box {\n  color: #fff;\n  font-size: 14px;\n}\n.login-info-box a,\n.register-info-box a {\n  color: #eee;\n}\n.login-info-box a:hover,\n.register-info-box a:hover {\n  color: #212121;\n  text-decoration: underline;\n  cursor: pointer;\n}\n.login-info-box span,\n.register-info-box span {\n  font-size: 12px;\n  display: block;\n}\n.login-reg-panel #label-login,\n.login-reg-panel #label-register {\n  background: transparent;\n  border: 2px solid #fff;\n  color: #fff;\n  font-size: 15px;\n  padding: 8px 20px;\n  transition: all 0.4s ease;\n}\n.login-reg-panel #label-login:hover,\n.login-reg-panel #label-register:hover {\n  background: #fff;\n  border: 2px solid #fff;\n  color: #212121;\n}\n.login-info-box h2,\n.register-info-box h2 {\n  font-size: calc(14px + (18 - 14) * ((100vw - 300px) / (1600 - 300)));\n  margin-bottom: 12px;\n}\n.show-mobile-only {\n  display: none;\n}\n@media (max-width: 1024px) {\n  .login-reg-panel {\n    width: 70%;\n  }\n\n  .white-panel {\n    right: calc(50% - 80px);\n  }\n}\n@media (max-width: 425px) {\n  .white-panel {\n    width: 100%;\n  }\n\n  .show-mobile-only .reg-info-box span {\n    margin-right: 0;\n  }\n}\n@media (max-width: 768.98px) {\n  .container-login {\n    padding: 40px 15px;\n  }\n\n  .wrap-login {\n    padding: 24px;\n  }\n\n  .wrap-input-field {\n    margin-bottom: 18px;\n  }\n\n  .login-header {\n    padding-bottom: 22px;\n    display: flex;\n  }\n\n  .login-form-title {\n    font-size: 2rem;\n  }\n\n  .login-logo {\n    width: 50px;\n  }\n\n  .login-reg-panel {\n    width: 100%;\n  }\n\n  .white-panel {\n    position: absolute;\n    top: 0;\n    right: 0 !important;\n    left: 0;\n    max-width: 390px;\n    width: 100%;\n    height: auto;\n    margin: 0 auto 24px;\n  }\n\n  .login-reg-panel {\n    width: 100%;\n    top: -40px;\n    background: transparent;\n  }\n\n  .show-mobile-only .reg-info-box {\n    text-align: center;\n    font-size: 12px;\n    color: #999;\n  }\n\n  .login-info-box .show-mobile-only .reg-info-box span {\n    margin-right: 12px;\n    display: block;\n  }\n\n  .show-mobile-only .reg-info-box a {\n    color: #999;\n  }\n\n  .show-mobile-only .reg-info-box a:hover {\n    color: #2c7fc6;\n    text-decoration: underline;\n    cursor: pointer;\n  }\n\n  .wrap-login {\n    padding: 0;\n  }\n\n  .show-mobile-only {\n    display: block;\n    width: 100%;\n  }\n\n  .login-info-box,\n.register-info-box {\n    display: none !important;\n    opacity: 0 !important;\n  }\n\n  .login-reg-panel .show-mobile-only #label-login,\n.login-reg-panel .show-mobile-only #label-register {\n    color: #212121;\n    display: block;\n    text-align: center;\n    width: 100%;\n    border: 2px solid #adadad;\n  }\n\n  .login-reg-panel .show-mobile-only #label-login:hover,\n.login-reg-panel .show-mobile-only #label-register:hover {\n    background: #212121;\n    border-color: #212121;\n    color: #fff;\n  }\n\n  .show-mobile-only label#label-login span {\n    margin-bottom: 4px;\n  }\n\n  .show-mobile-only label#label-login i {\n    margin-left: 8px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */";