import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login';
import { AuthGuard } from './_guards';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HomeComponent } from './home/home.component';
import { SamplePageComponent } from './sample-page/sample-page.component';
import { FormComponent } from './form/form.component';
import { TableComponent } from './table/table.component';
import { MsfStyleOneComponent } from './design-components/multi-step-form/msf-style-one/msf-style-one.component';
import { MsfStyleTwoComponent } from './design-components/multi-step-form/msf-style-two/msf-style-two.component';
import { MsfStyleThreeComponent } from './design-components/multi-step-form/msf-style-three/msf-style-three.component';
import { MsfStyleFourComponent } from './design-components/multi-step-form/msf-style-four/msf-style-four.component';
import { LoginStyleOneComponent } from './design-components/login-design/login-style-one/login-style-one.component';
import { LoginStyleTwoComponent } from './design-components/login-design/login-style-two/login-style-two.component';
import { LoginStyleThreeComponent } from './design-components/login-design/login-style-three/login-style-three.component';
import { LoginStyleThreeRegistrationComponent } from './design-components/login-design/login-style-three-registration/login-style-three-registration.component';
import { LoginStyleThreeForgotPasswordComponent } from './design-components/login-design/login-style-three-forgot-password/login-style-three-forgot-password.component';
import { DataTableStyleComponent } from './design-components/tables/data-table-style/data-table-style.component';

const appRoutes: Routes = [
    { path: '', component: DashboardComponent, canActivate: [AuthGuard] },
    { path: 'login', component: LoginComponent },
    { path: 'home', component: HomeComponent },
    { path: 'sample-page', component: SamplePageComponent },
    { path: 'form', component: FormComponent },
    { path: 'table', component: TableComponent },
    { path: 'msf-one', component: MsfStyleOneComponent },
    { path: 'msf-two', component: MsfStyleTwoComponent },
    { path: 'msf-three', component: MsfStyleThreeComponent },
    { path: 'msf-four', component: MsfStyleFourComponent },
    { path: 'login-style-one', component: LoginStyleOneComponent },
    { path: 'login-style-two', component: LoginStyleTwoComponent },
    { path: 'login-style-three', component: LoginStyleThreeComponent },
    { path: 'login-style-three-registration', component: LoginStyleThreeRegistrationComponent },
    { path: 'login-style-three-forgotpassword', component: LoginStyleThreeForgotPasswordComponent },
    { path: 'datatable', component: DataTableStyleComponent },
    {
        path: 'business-house-info',
        loadChildren: './CMS/business-house-info/business-house-information/business-house-information.module#BusinessHouseInformationModule'
    },
    {
        path: 'internal-user',
        loadChildren: './CMS/business-house-info/user-mgmt/user-mgmt.module#UserMgmtModule'
    },

    // otherwise redirect to home
    { path: '**', redirectTo: '' }
];

export const routing = RouterModule.forRoot(appRoutes, { useHash: true, scrollPositionRestoration: 'enabled' });