import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-login-style-three-forgot-password',
  templateUrl: './login-style-three-forgot-password.component.html',
  styleUrls: ['./login-style-three-forgot-password.component.scss']
})
export class LoginStyleThreeForgotPasswordComponent implements OnInit {

  constructor() { }

  ngOnInit() {


    ///login form animations and translations
    $(document).ready(function () {
      $('.login-info-box').fadeOut();
      $('.login-show').addClass('show-log-panel');
    });

    $('#sendEmailBtn').on('click', function (e) {
      $('.register-info-box').fadeIn();
      $('.login-info-box').fadeOut();

      $('.white-panel').addClass('right-log');
      $('.register-show').addClass('show-log-panel');
      $('.login-show').removeClass('show-log-panel');
    });
    $('#resetPasswordBtn').on('click', function (e) {
      $('.login-info-box').fadeIn();
      $('.register-info-box').fadeOut();

      $('.white-panel').removeClass('right-log');
      $('.register-show').removeClass('show-log-panel');
      $('.login-show').addClass('show-log-panel');
    });

    //scroll top
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;

  }

}
