import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'app-msf-style-two',
  templateUrl: './msf-style-two.component.html',
  styleUrls: ['./msf-style-two.component.scss']
})
export class MsfStyleTwoComponent implements OnInit {

  constructor() { }

  ngOnInit() {

    $(document).ready(function () {
      //Dynamic Next
      $(".btn-next").on("click", function () {
        const nextStep = $("#" + $(this).parents(".slider-step").data("nextStep"));
        $(this).parents(".slider-step").attr("data-anim", "hide-to--left");
        nextStep.attr("data-anim", "show-from--right");
      });

      //Dynamic Back
      $(".btn-back").on("click", function () {
        const backTo = $("#" + $(this).parents(".slider-step").data("backTo"));
        $(this).parents(".slider-step").attr("data-anim", "hide-to--right");
        backTo.attr("data-anim", "show-from--left");
      });
    });

  }

}
